import React from 'react'
import './Main.css'
import {Link } from "react-router-dom";

function Main() {
  return (
    <div className="main">
        <nav>
            <div className="logo"><h2>Enefti</h2></div>
            <ul>
                <li>Home</li>
                <li>Collectors</li>
                <li>NFT Marketplace</li>
                <li>Features</li>
                <li>Pages</li>
                <li>Help</li>
                <li>Purchase</li>
            </ul>
            <Link to="/login">
            <button id="btn"><i className="fa-solid fa-magnifying-glass"></i>Sign In</button>
            </Link>
        </nav>

        <div className="container">
         <div className="main_box_1">
           <div className="box_1">
                <div className="sub_box"></div>
           </div>
           <div className="box_2">
             <div className="sub_box_1">
                 <div className="sub_box_2">
                    <h3>Consciousness</h3>
                    <h6>Suspended in a sunbeam<br></br>
                    globular star cluster the ash of<br></br>
                    stellar alchemy.</h6>
                 </div>
             </div>
             <div className="sub_box_1">
                <div className="sub_box_2">
                   <h3>Glorious Dawn</h3>
                   <h6>With pretty stories for which<br></br>
                    there's little good evidence<br></br>
                   tendrills of gossamer clouds.</h6>
                </div>
            </div>
            <div className="sub_box_1 last_box">
                <div className="sub_box_2 last_box_1">
                   <h3>Intelligent</h3>
                   <h6>Uranus made in the interiors of<br></br>
                   collapsing stars take root and<br></br>
                   flourish.</h6>
                </div>
            </div>
           </div>
         </div>
           <div className="main_box_2">
               <div className="sub_box_3">
                  <h1>METAVERSE <br></br>
                TO BE A MUCH <br></br>
                MORE <br></br>
                EVOLVED <br></br>
                SPACE </h1>
                <h3>The metaverse is the hypothesized next <br></br>
                iteration of the internet,supporting<br></br>
                decentralized persistent online 3-D virtual<br></br>
                environments.</h3>
                <button className="main_btn">READ THE STORY</button>
               </div>
               <div className="sub_box_4">
                   <div className="telegram_box">
                    <i className="fa-brands fa-telegram icon"></i>
                   </div>
                   <div className="message_box">
                    <i className="fa-regular fa-message icons"></i>
                   </div>
               </div>
           </div>

        </div>

    </div>
  )
}

export default Main